.field-group__list {
  margin: 0;
  padding: 0;
  clear: both;
  list-style: none;
}

.field-group__list--contacts {
  @media(min-width: $breakpoint-large) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 80px;
  }
}

.field-group__list--additional {
  @media(min-width: $breakpoint-medium) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 80px;
  }

  @media(min-width: $breakpoint-large) {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }
}

.field-group__item {
  & + & {
    margin-top: 14px;

    @media (min-width: $breakpoint-medium) {
      margin-top: 20px;

      .field-group--target & {
        margin-top: 13px;
      }
    }
  }
}
